<script>
import { mapActions, mapState } from "vuex";
import PostItem from "../views/Postitem.vue";
import Panel from "../../containers/Panel.vue";
import axios from "../plugins/axios";
export default {
  components: { Panel, PostItem },
  data() {
    return {
      recent_replies: [],
      recent_topics: [],
    };
  },
  computed: {
    ...mapState({
      user: (state) => state.user.user,
    }),
  },
  watch: {
    "$route.params.username": {
      handler(newValue) {
        this.getUser(newValue);
      },
      immediate: true,
    },
    // ÂΩì user ÂèëÁîüÊîπÂèòÁöÑÊó∂ÂÄôÔºåÈúÄË¶ÅÈáçÊñ∞Ëé∑ÂèñÁõ∏ÂÖ≥ÁöÑ ÂèÇ‰∏éÁöÑËØùÈ¢ò Âíå ÂàõÂª∫ÁöÑËØùÈ¢ò ËØ¶ÊÉÖ
    user: {
      // ÁõëÂê¨ user ÁöÑÂèòÂåñ
      // 1. È¶ñÊ¨°ËøõÂÖ• user È°µÔºåÈªòËÆ§ user ÊòØ null ÁÑ∂Âêé action Ëß¶Âèë‰πãÂêé user Êõ¥Êñ∞‰∫Ü
      // 2. ÂΩìÂú®ÂΩìÂâçÈ°µÂàáÊç¢ÂÖ∂‰ªñÁî®Êà∑
      async handler(newValue) {
        // Ë¶ÅÊ≥®ÊÑèËØ∑Ê±ÇÈúÄË¶ÅÂèëÂ§ö‰∏™ËÄå‰∏îË¶ÅÁ≠âÂæÖÊâÄÊúâËØ∑Ê±ÇÁªìÊùüÊâçËÉΩÊâßË°åÂÖ∂‰ªñÊìç‰Ωú
        const res1 = await axios.all(
          newValue.recent_replies.map((topic) =>
            axios.get(`/topic/${topic.id}`)
          )
        );
        this.recent_replies = res1.map((ele) => ele.data);
        const res2 = await axios.all(
          newValue.recent_topics.map((topic) => axios.get(`/topic/${topic.id}`))
        );
        this.recent_topics = res2.map((ele) => ele.data);
      },
    },
  },
